.item {
  padding: 13px 15px;
  border-radius: 4px;
  border: 1px rgba(31, 32, 65, 0.25) solid;
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: rgba(31, 32, 65, 0.75);
  line-height: calc(18/14 * 100%);
  &_small {
    @media (max-width: 420px) {
      padding: 13px 4px;
    }
  }
  &_hover {
    border: 1px rgba(31, 32, 65, 0.5) solid;
  }
}